import './RadioFilter.css';

function RadioFilter({ options, title, register, nameInput, errors, errorValue }) {

  return (
    <div className='filter-radio'>
      {title && <h3 className='filter-radio__title'>{title}</h3>}
        <div className='filter-radio__container'>
          {options.slice(0, options.length).map((option, index) => (
          <label className='filter-radio__label' key={option.id}>
            <input
              {...register(nameInput,
                errorValue && {
                required: errorValue
                })}
              type='radio'
              value={option.value}
              className='filter-radio__input filter-radio__input_invisible'
            />
            <span className='filter-radio__input_visible'/>
            <p className='filter-radio__label-subtitle'>{option.label}</p>
          </label>
          ))}
        </div>
        {errors &&
        errors[nameInput] && (
          <div className='input__error'>
            {errors[nameInput].message}
          </div>
        )}
    </div>
  );
}

export default RadioFilter;