import './BodyInfoInput.css';

function BodyInfoInput({ 
  title,
  placeholder,
  inputClassName,
  nameInput,
  errorValue,
  value,
  message,
  type,
  register,
  errors,
  minLengthError, 
  minLength }) {

  return (
    <label className={'input__title ' + inputClassName}>
      {title}
      <input className='input'
        placeholder={placeholder}
        type={type}
        {...register(nameInput, {
          required: errorValue,
          pattern: {
            value: value,
            message: message
          },
          minLength: {
            value: minLength,
            message: minLengthError,
          },
        })}
      />
      {errors[nameInput] && (
        <div className='input__error'>
          {errors[nameInput].message}
        </div>
      )}
    </label>
  );
}

export default BodyInfoInput;