import './GeneralInfoInput.css';

function GeneralInfoInput({ 
  title,
  placeholder,
  inputClassName,
  nameInput,
  errorValue,
  value,
  message,
  type,
  register,
  errors }) {

  return (
    <label className={'input__title ' + inputClassName}>
      {title}
      <input
        className='input'
        placeholder={placeholder}
        type={type}
        {...register(nameInput, {
          required: errorValue,
          pattern: {
            value: value,
            message: message
          }
        })}
      />
      {errors[nameInput] && (
        <div className='input__error'>
          {errors[nameInput].message}
        </div>
      )}
    </label>
  );
}

export default GeneralInfoInput;