import './AddMoney.css';
import Popup from './Popup/Popup';
import { useState } from 'react';
import { useForm } from "react-hook-form";
import { ADD_MONEY_OPTIONS } from '../../../constants/filterOptions'

function AddMoney() {
  const [isPopupOpen, setIsPopupOpen] = useState(false);

  const { 
    register, handleSubmit, formState: { errors },
  } = useForm({
    mode: "onChange"
  });

  const closePopup = () => {
    setIsPopupOpen(false)
  }

  const handleButtonOpenPopup = (e) => {
    setIsPopupOpen(true)
  }

  const onSubmit = (data) => {
    handleButtonOpenPopup()
  }

  return (
    <>
      <form className='tabs__form add-money' onSubmit={handleSubmit(onSubmit)}>
        <p className='add-money__ratio'><span className='add-money__ratio-icon'/>Курс: 1 каст = 1 рубль</p>
        <div className='add-money__container'>
        <label className='input__title input__title_limited_width'>
          Сумма пополнения
          <input
            {...register('sumValue', {
              required: 'Поле обязательно к заполнению',
              min: {
                value: 1,
                message: "Введите положительное значение",
              },
            })}
            className='input'
            placeholder='Введите сумму'
            type='number'
          />
          {errors?.sumValue && (
            <div className='input__error'>
              {errors.sumValue.message}
            </div>
          )}
          </label>
          <div className='filter-radio'>
            <h3 className='filter-radio__title'>Способ оплаты</h3>
            <div className='filter-radio__container'>
              <label className='filter-radio__label'>
                <input
                  {...register('typeAddMoney')}
                  type='radio'
                  name='addMoneyType'
                  value={ADD_MONEY_OPTIONS[0].value}
                  className='filter-radio__input filter-radio__input_invisible'
                  defaultChecked
                />
                <span className='filter-radio__input_visible'/>
                <p className='filter-radio__label-subtitle'>{ADD_MONEY_OPTIONS[0].label}</p>
              </label>
              <label className='filter-radio__label'>
                <input
                  {...register('typeAddMoney')}
                  type='radio'
                  name='addMoneyType'
                  value={ADD_MONEY_OPTIONS[1].value}
                  className='filter-radio__input filter-radio__input_invisible'
                />
                <span className='filter-radio__input_visible'/>
                <p className='filter-radio__label-subtitle'>{ADD_MONEY_OPTIONS[1].label}</p>
              </label>
            </div>
          </div>
        </div>
        <div className='add-money__card-data'>
          <label className='add-money__label add-money__label_card-number'>
            <input
              {...register('cardNumber', {
                required: 'Поле обязательно к заполнению',
              })}
              className='add-money__input add-money__input_card-number'
              placeholder='Номер карты'
              type='number'
            />
            {errors?.cardNumber && (
              <div className='input__error'>
                {errors.cardNumber.message}
              </div>
            )}
          </label>
          <label className='add-money__label add-money__label_card-owner'>
            <input
              {...register('cardOwner', {
                required: 'Заполните поле',
              })}
              className='add-money__input add-money__input_owner'
              placeholder='Владелец'
              type='number'
            />
            {errors?.cardOwner && (
              <div className='input__error'>
                {errors.cardOwner.message}
              </div>
            )}
          </label>
          <label className='add-money__label add-money__label_card-date'>
            <input
              {...register('cardDate', {
                required: 'Заполните поле',
              })}
              className='add-money__input add-money__input_date'
              placeholder='00/00'
              type='number'
            />
            {errors?.cardDate && (
              <div className='input__error'>
                {errors.cardDate.message}
              </div>
            )}
          </label>
          <label className='add-money__label add-money__label_card-cvc'>
            <input
              {...register('cardCvc', {
                required: 'Заполните поле',
              })}
              className='add-money__input add-money__input_cvc'
              placeholder='CVC'
              type='text'
            />
            {errors?.cardCvc && (
              <div className='input__error'>
                {errors.cardCvc.message}
              </div>
            )}
          </label>
        </div>
        <button type='submit' className='add-money__button'>Пополнить</button>
      </form>
      <Popup onClose={closePopup} isOpen={isPopupOpen}/>
    </>
  )
}

export default AddMoney;