import './InterestsInputs.css';
import Select from 'react-select';
import { Controller } from "react-hook-form";
import TextareaInput from '../TextareaInput/TextareaInput';
import { getValue } from '../../utils/getSelectValue';
import { REQUIRED_ERROR } from '../../constants/errors';

import {
  SMOKING_ATTITUDE_OPTIONS,
  ALCOHOL_ATTITUDE_OPTIONS,
  PSYCHOTROPIC_SUBSTANCES_ATTITUDE_OPTIONS,
  ORIENTATION_TYPE_OPTIONS,
  MARITAL_STATUS_OPTIONS,
  CHILDREN_STATUS_OPTIONS
} from '../../constants/filterOptions'

function InterestsInputs({ interestsFilterClassName, control, errors, register }) {
  const questionsClassName = (
    `${interestsFilterClassName ? interestsFilterClassName : ''}`
  )
  
  return (
    <div className={questionsClassName}>
      <div className='questionary__questions questionary__questions_step_third'>
        <div className='questionary__container questionary__container_attitude'>
        <Controller
          control={control}
          name='smokingAttitude'
          rules={{
            required: REQUIRED_ERROR
          }}
          render={({ field: {onChange, value}, fieldState: { error}}) => (
            <label className='custom-select__label'>
              Отношение к курению
              <Select
                value={getValue(value, SMOKING_ATTITUDE_OPTIONS)}
                classNamePrefix='custom-select'
                options={SMOKING_ATTITUDE_OPTIONS}
                placeholder='Все'
                isSearchable={false}
                onChange={newValue => onChange((newValue).value)}
              />
              {error && (
              <div className='input__error'>
                {error.message}
              </div>
              )}
            </label>
          )}
        />
        <Controller
          control={control}
          name='alcoholAttitude'
          rules={{
            required: REQUIRED_ERROR
          }}
          render={({ field: {onChange, value}, fieldState: { error}}) => (
            <label className='custom-select__label'>
              Отношение к алкоголю
              <Select
                value={getValue(value, ALCOHOL_ATTITUDE_OPTIONS)}
                classNamePrefix='custom-select'
                options={ALCOHOL_ATTITUDE_OPTIONS}
                placeholder='Все'
                isSearchable={false}
                onChange={newValue => onChange((newValue).value)}
              />
              {error && (
              <div className='input__error'>
                {error.message}
              </div>
              )}
            </label>
          )}
        />
        <Controller
          control={control}
          name='psychotropicSubstancesAttitude'
          rules={{
            required: REQUIRED_ERROR
          }}
          render={({ field: {onChange, value}, fieldState: { error}}) => (
            <label className='custom-select__label'>
              Отношение к психотропным веществам
              <Select
                value={getValue(value, PSYCHOTROPIC_SUBSTANCES_ATTITUDE_OPTIONS)}
                classNamePrefix='custom-select'
                options={PSYCHOTROPIC_SUBSTANCES_ATTITUDE_OPTIONS}
                placeholder='Все'
                isSearchable={false}
                onChange={newValue => onChange((newValue).value)}
              />
              {error && (
              <div className='input__error'>
                {error.message}
              </div>
              )}
            </label>
          )}
        />
        </div>
        <div className='questionary__container questionary__container_interests'>
          <Controller
            control={control}
            name='orientation'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Ориентация
                <Select
                  value={getValue(value, ORIENTATION_TYPE_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={ORIENTATION_TYPE_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
          <Controller
            control={control}
            name='maritalStatus'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Семейное положение
                <Select
                  value={getValue(value, MARITAL_STATUS_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={MARITAL_STATUS_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
          <Controller
            control={control}
            name='childrens'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Наличие детей
                <Select
                  value={getValue(value, CHILDREN_STATUS_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={CHILDREN_STATUS_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
        </div>
      </div>
      <div className='questionary__text-areas'>
        <TextareaInput
          title={'Увлечения'}
          placeholder={'Чем вы любите заниматься'}
          nameInput={'hobbies'}
          errorValue={REQUIRED_ERROR}
          register={register}
          errors={errors}
        />
        <TextareaInput
          title={'О себе'}
          placeholder={'Напишите о себе'}
          nameInput={'about'}
          errorValue={REQUIRED_ERROR}
          register={register}
          errors={errors}
        />
      </div>
    </div>
  )
}

export default InterestsInputs;