import './BodyInfoInputs.css';
import Select from 'react-select';
import { Controller } from "react-hook-form";
import { getValue } from '../../utils/getSelectValue';
import BodyInfoInput from './BodyInfoInput/BodyInfoInput';
import { REQUIRED_ERROR, LENGTH_ERROR } from '../../constants/errors';
import {
  BODY_TIPE_OPTIONS,
  SKIN_TIPE_OPTIONS,
  BODY_HAIR_OPTIONS,
  LOOKS_TYPE_OPTIONS,
  HAIR_LENGTH_OPTIONS,
  HAIR_FORM_OPTIONS,
  GENDER_OPTIONS
} from '../../constants/filterOptions'

function BodyInfoInputs ({ bodyFilterClassName, register, control, errors }) {

  const questionsClassName = (
    `${bodyFilterClassName ? bodyFilterClassName : ''}`
  )

  return (
    <div className={questionsClassName}>
      <div className='filter-radio'>
        <h3 className='filter-radio__title'>Пол</h3>
          <div className='filter-radio__container'>
            {GENDER_OPTIONS.slice(1, GENDER_OPTIONS.length).map((option, index) => (
            <label className='filter-radio__label' key={option.id}>
              <input
                {...register('gender', {
                  required: REQUIRED_ERROR,
                })}
                type='radio'
                value={option.value}
                className='filter-radio__input filter-radio__input_invisible'
              />
              <span className='filter-radio__input_visible'/>
              <p className='filter-radio__label-subtitle'>{option.label}</p>
            </label>
            ))}
            {errors.gender && (
            <div className='input__error'>
              {errors.gender.message}
            </div>
            )}
          </div>
        </div>
        <div className='questionary__questions questionary__questions_step_second'>
          <BodyInfoInput
            title={'Объем талии, см'}
            placeholder={'Введите значение'}
            nameInput={'waist'}
            errorValue={REQUIRED_ERROR}
            type='number'
            register={register}
            errors={errors}
            minLength ={'2'}
            minLengthError={LENGTH_ERROR}
          />
          <BodyInfoInput
            title={'Объем бедер, см'}
            placeholder={'Введите значение'}
            nameInput={'hips'}
            errorValue={REQUIRED_ERROR}
            type='number'
            register={register}
            errors={errors}
            minLength ={'2'}
            minLengthError={LENGTH_ERROR}
          />
          <Controller
            control={control}
            name='body'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Телосложение
                <Select
                  value={getValue(value, BODY_TIPE_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={BODY_TIPE_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
          <Controller
            control={control}
            name='skin'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Цвет кожи
                <Select
                  value={getValue(value, SKIN_TIPE_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={SKIN_TIPE_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
          <Controller
            control={control}
            name='bodyHair'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Наличие волос на теле
                <Select
                  value={getValue(value, BODY_HAIR_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={BODY_HAIR_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
          <Controller
            control={control}
            name='looks'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Внешность
                <Select
                  value={getValue(value, LOOKS_TYPE_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={LOOKS_TYPE_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
          <Controller
            control={control}
            name='hairLength'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Длина волос
                <Select
                  value={getValue(value, HAIR_LENGTH_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={HAIR_LENGTH_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
          <Controller
            control={control}
            name='hairForm'
            rules={{
              required: REQUIRED_ERROR
            }}
            render={({ field: {onChange, value}, fieldState: { error}}) => (
              <label className='custom-select__label'>
                Форма волос
                <Select
                  value={getValue(value, HAIR_FORM_OPTIONS)}
                  classNamePrefix='custom-select'
                  options={HAIR_FORM_OPTIONS}
                  placeholder='Все'
                  isSearchable={false}
                  onChange={newValue => onChange((newValue).value)}
                />
                {error && (
                <div className='input__error'>
                  {error.message}
                </div>
                )}
              </label>
            )}
          />
        </div>
    </div>
  )
}

export default BodyInfoInputs;