import './WithdrawFunds.css';
import { useState, useEffect } from 'react';
import { useForm } from "react-hook-form";
import {  WITHDRAWFUNDS_OPTIONS } from '../../../constants/filterOptions'

function WithdrawFunds() {
  const [sum, setSum] = useState();
  const [focus, setFocus] = useState(false);
  const [toggleValue, setToggleValue] = useState('card');

  const { 
    register, handleSubmit, formState: { errors }, watch,
  } = useForm({
    mode: "onChange"
  });
  
  const onSubmit = (data) => {

  }

  const commissionClassName = (
    `${focus ? 'withdraw__commission' : 'withdraw__commission_invisible'}`
  );

  const watchSumValue = watch('sumValue')

  useEffect(() => {
    setSum(watchSumValue)
  }, [watchSumValue])

  const handleChangeTypeAccount = (e) => {
    setToggleValue(e.target.value)
  }

  const handleSumFocus = (e) => {
    setFocus(true)
  };

  const handleSumBlur = (e) => {
    setFocus(false)
  };

  const commissionValue = () => {
    if(typeof(sum) === 'undefined') {
      return 0
    } else {
      return `${sum * 5 /100}`
    }
  }

  return (
    <form className='withdraw tabs__form' onSubmit={handleSubmit(onSubmit)}>
      <div className='withdraw__info-container'>
        <h2 className='withdraw__title'>Вывод средств</h2>
        <p className='withdraw__subtitle'>Минимальная сумма вывода составляет 1000 кастов + 5% комиссия сервиса. Сам банк также может назначать комиссию за перевод.</p>
        <p className='withdraw__ratio'><span className='withdraw__ratio-icon'/>Курс: 1 каст = 1 рубль</p>
      </div>
      <div className='withdraw__sum-container'>
        <label className='input__title'>
          Сумма вывода
          <input
            {...register('sumValue', {
              required: 'Поле обязательно к заполнению',
              min: {
                value: 1,
                message: "Введите положительное значение",
              },
            })}
            className='input'
            placeholder='Введите сумму'
            onFocus={handleSumFocus}
            onBlur={handleSumBlur}
            type='number'
          />
          {errors?.sumValue && (
            <div className='input__error'>
              {errors.sumValue.message}
            </div>
          )}
        {errors.sumValue ?
          ''
          :
          <p className={commissionClassName}>Комиссия составит: {commissionValue()} рублей</p>
        }
        </label>
        <div className='filter-radio'>
          <h3 className='filter-radio__title'>Куда вывести</h3>
          <div className='filter-radio__container'>
            <label className='filter-radio__label'>
              <input
                {...register('typeAccount')}
                type='radio'
                name='accuntType'
                value={WITHDRAWFUNDS_OPTIONS[0].value}
                className='filter-radio__input filter-radio__input_invisible'
                onChange={handleChangeTypeAccount}
                defaultChecked
              />
              <span className='filter-radio__input_visible'/>
              <p className='filter-radio__label-subtitle'>{WITHDRAWFUNDS_OPTIONS[0].label}</p>
            </label>
            <label className='filter-radio__label'>
              <input
                {...register('typeAccount')}
                type='radio'
                name='accuntType'
                value={WITHDRAWFUNDS_OPTIONS[1].value}
                className='filter-radio__input filter-radio__input_invisible'
                onChange={handleChangeTypeAccount}
              />
              <span className='filter-radio__input_visible'/>
              <p className='filter-radio__label-subtitle'>{WITHDRAWFUNDS_OPTIONS[1].label}</p>
            </label>
          </div>
        </div>
      </div>
        {
          toggleValue === 'card' ?
          <div className='withdraw__data-container'>
            <label className='input__title input__title_limited_width'>
              Номер карты
              <input
                {...register('cardNumber', {
                  required: 'Поле обязательно к заполнению',
                })}
                className='input'
                placeholder='Введите номер'
                type='number'
              />
              {errors?.cardNumber && (
                <div className='input__error'>
                  {errors.cardNumber.message}
                </div>
              )}
            </label>
            <label className='input__title input__title_limited_width'>
              Владелец каты
              <input
                {...register('cardOwner', {
                  required: 'Поле обязательно к заполнению',
                })}
                className='input'
                placeholder='Как на карте'
                type='text'
              />
              {errors?.cardOwner && (
                <div className='input__error'>
                  {errors.cardOwner.message}
                </div>
              )}
            </label>
          </div>
          :
          <div className='withdraw__data-container'>
            <label className='input__title input__title_limited_width'>
              Номер счета
              <input
                {...register('account', {
                  required: 'Поле обязательно к заполнению',
                })}
                className='input'
                placeholder='Номер вашего счета'
                type='number'
              />
              {errors?.account && (
                <div className='input__error'>
                  {errors.account.message}
                </div>
              )}
            </label>
            <label className='input__title input__title_limited_width'>
              Название банка
              <input
                {...register('bank', {
                  required: 'Поле обязательно к заполнению',
                })}
                className='input'
                placeholder='Банк'
                type='number'
              />
              {errors?.bank && (
                <div className='input__error'>
                  {errors.bank.message}
                </div>
              )}
            </label>
            <label className='input__title input__title_limited_width'>
              БИК
              <input
                {...register('bik', {
                  required: 'Поле обязательно к заполнению',
                })}
                className='input'
                placeholder='БИК банка'
                type='number'
              />
              {errors?.bik && (
                <div className='input__error'>
                  {errors.bik.message}
                </div>
              )}
            </label>
          </div>
        }
      <button className='withdraw__button' type='submit'>Вывести</button>
    </form>
  )
}

export default WithdrawFunds;